import styled, { keyframes } from 'styled-components';

const rotate = keyframes`
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
`;

export const Loading = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  i {
    animation: ${rotate} 2s infinite;
  }
`;
